import Menu from '../components/Menu';
import Blog from '../components/Blog';
import Footer from '../components/Footer';

const BlogPage = () => {
  return (
    <GlobalStyles>
      <Menu />
      <div>
        <GlobalHead pageName="Blog" />
        <Blog />
      </div>
      <Footer />
      <style jsx>{`
        body {
          height: 100%;
        }
      `}</style>
    </GlobalStyles>
  );
};

BlogPage.propTypes = {};

export default BlogPage;



